.burger {
  display: block;
  border: 0;
  width: 24px;
  height: 18px;
  position: relative;
  cursor: pointer;
  background-color: inherit;
  transition: 0.5s;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  right: 0;
  background-color: #000000;
  transition: 0.5s;
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}

.burger__line {
  position: absolute;
  width: 24px;
  top: 8px;
  right: 0;
  height: 2px;
  background-color: #000000;
}

.header__content {
  max-width: 100%;
}

.header__nav {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  z-index: 5;
  padding-top: 14px;
  padding-bottom: 14px;
}

.header__logo {
  margin-left: 20px;
}

.header__menu {
  position: absolute;
  width: 100%;
  z-index: 5;
  top: 55px;
  background-color: #ffffff;
  transition: 0.5s;
  padding-top: 12px;
  opacity: 0;
  visibility: hidden;
}

.header__menu--no-js {
  position: static;
  opacity: 1;
  visibility: visible;
}

.header__burger {
  margin-right: 20px;
}

.header__item {
  border-top: 1px solid #e6e6e6;
}

.header__link {
  width: 100%;
  display: block;
  padding: 24px 0;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
}

.header__active {
  transition: 0.5s;
}
.header__active::before {
  transition: 0.5s;
  top: 8px;
  transform: rotate(-45deg);
}
.header__active::after {
  transition: 0.5s;
  bottom: 8px;
  transform: rotate(45deg);
}
.header__active .header__line {
  transition: 0.5s;
  opacity: 0;
}

.header__active + .header__menu {
  transition: 0.5s;
  opacity: 1;
  visibility: visible;
}

